import React, { Component } from 'react';

import {
    Row,
    Col
} from "reactstrap";


class CardRow extends Component {

  render() {
      return (
              <Row className="padding-10">
              <Col xs="12" sm="6" lg="6">
              <h2>{this.props.version.id}</h2>
              </Col>
              <Col xs="12" sm="6" lg="6">
              <h2><a href={this.props.version.releaseNotesUrl} className="text-white nav-link float-right" target="_blank">{this.props.version.latest}</a></h2>
              </Col>
              </Row>
    )
  }
}

export default CardRow;



